// scripts/settings.js

const settingsBtn = document.querySelector("#toggle-settings");
const settingsMenuContainer = document.querySelector(".settings-menu-container");

settingsBtn.addEventListener("click", () => {
    if (chatMenu.classList.contains("open")) {
        if (chatMenu.classList.contains("conversations-open")) {
            chatMenu.classList.add("settings-open");
            chatMenu.classList.remove("conversations-open");
        } else {
            chatMenu.classList.remove("settings-open");
            chatMenu.classList.add("conversations-open");
        }

    }
    toggleMenu();
});

const themes = ["light", "dark", "system"];
let chosenTheme = 2;

const darkModeMediaQuery = window.matchMedia("(prefers-color-scheme: dark)");

function applyTheme(e) {
    document.body.classList.remove('light');
    document.body.classList.remove('dark');
    if (themes[chosenTheme] == "light") {
        document.body.classList.add('light');
    } else if (themes[chosenTheme] == "dark") {
        document.body.classList.add('dark');
    } else if (themes[chosenTheme] == "system") {
        if (e.matches) {
            document.body.classList.add('dark');
        } else {
            document.body.classList.add('light');
        }
    }
    console.log("applied theme:", themes[chosenTheme])
}

const themeOptions = document.querySelectorAll("#theme-select li");

for (let option of themeOptions) {
    option.addEventListener("click", () => {
        chosenTheme = themes.indexOf(option.dataset.value);
        applyTheme(darkModeMediaQuery);
    });
}


if (darkModeMediaQuery.addEventListener) {
    darkModeMediaQuery.addEventListener('change', applyTheme);
} else if (darkModeMediaQuery.addListener) {
    darkModeMediaQuery.addListener(applyTheme);
}